import React, { useState, useEffect } from 'react';
import DataForm from './DataForm';

const Form = ({newName}) => {
    const [name, setName] = useState('');

    const [phone, setPhone] = useState('');

    const [Datas, setDatas] = useState([])


    function handleName(e) {
        setName(e.target.value);
    };

    function handlePhone(e) {
        setPhone(e.target.value);
    };

    function addData(name, phone) {
        const newRegister = {name: name, phone: phone, id: name + phone}
        setDatas([...Datas, newRegister])
        resetValues();
    }

    function resetValues(){
        setPhone('');
        setName('');
    }

    const dataList = Datas.map(Data => (
        <DataForm 
            id={Data.id}
            key={Data.id}
            name={Data.name}
            phone={Data.phone}
            setDatas={setDatas}
            Datas={Datas}
        />
    ))


    return (
        <div className="form">
            <form onSubmit={newName}>
            <h1>CRUD</h1>
                <label htmlFor="username">
                    Nome
                    <input 
                        type="text"
                        name="name"
                        id="username"
                        placeholder="Ex: Gabriel Jodas"
                        onChange={handleName}
                    />
                </label>

                <input 
                    type="tel"
                    name="cellphone"  
                    id="userphone" 
                    placeholder="Ex: 11 98765-4321" 
                    onChange={handlePhone}
                    />
                <button 
                    id="submitBtn" 
                    type="submit"
                    onClick={() => addData(name,phone)}>
                    Cadastrar
                </button>
            </form>
            
            <div className="users-datas">
                <input type="text" id="search-name" name="search-name" placeholder="Buscar"/>
                {dataList}
            </div>    
        </div>
    )
}

export default Form;